body {
  margin: 0;
  background-color: #FBFBFB ;
  font-family: "Poppins";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-img{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  object-fit:cover;
  z-index: -1;
}


.cover{
  position: relative;
  height: 480px;
  background: linear-gradient(#9D0E0E00, #4F070799);
  color: white;
}

.cl-db{
  color: #192F60 !important;
}

.cl-dg{
  color: #8C96AB;
}

#searchIcon{
  position: absolute;
  left: 1rem;
}

#logo{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 140px;
  font-size: 70px;
}

#inpLocation{
  border: none;
  min-height: 58px;
}

.title{
  font-size: 2rem;
  font-weight: 400;
}

.navbar{
  z-index: 1;
}

#login:hover{
  color: #dc3545 !important;
  border-bottom: 2px solid #dc3545 !important;
  transition: 0.15s;
}

#register{
  min-width: 200px !important;
  cursor: pointer;
  transition: 0.15s;
}

#register:hover{
  background-color: white !important;
  color: #dc3545 !important;
  transition: 0.15s;
}

.searchItem{
  display: flex;
  align-items: center;
  background-color: white;
  min-height: 160px;
  padding: 0 !important;
  box-shadow: 0px 3px 6px #00000029;
  transition: 0.15s;
}

.searchItem img{
  min-width: 45%;
  height: 160px;
  object-fit: cover;
}

.searchItem div h5{
  font-size: 18px;
}
.searchItem p{
  font-size: 13px;
  font-weight: normal;
}

.searchItem:hover{
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
  transition: 0.15s;
}

@media (max-width: 769px) {
  #logo{
    width: 90px;
    height: 90px;
    font-size: 42px;
  }

  .title{
    font-size: 28px;
  }

  #inpLocation{
    min-width: 37px;
  }
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

